import { Component, OnInit } from '@angular/core';
import { AbsensiJadwalPegawaiTerjadwalService, AbsensiJadwalTipeService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { PegawaiDetailService } from '../../service/pegawai-detail.service';
import { NzMessageService } from 'ng-zorro-antd';
import { Jadwal_tipe, Pegawai, Jadwal_pegawai_terjadwal } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-edit-manual',
    templateUrl: './edit-manual.component.html',
    styleUrls: ['./edit-manual.component.scss']
})
export class EditManualComponent implements OnInit {

    constructor(
        private absensiJadwalTipeService: AbsensiJadwalTipeService,
        private absensiJadwalPegawaiTerjadwalService: AbsensiJadwalPegawaiTerjadwalService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private pegawaiDetailService: PegawaiDetailService,
        private nzMessageService: NzMessageService,
    ) { }

    id_pegawai: string = ''
    id_jadwal_tipe: string = null
    listTipe: Jadwal_tipe[] = []
    ListJadwal: Jadwal_pegawai_terjadwal = {}

    bulan: string = null;
    tahun: string = null;
    ngOnInit() {

        this.activatedRoute.paramMap.subscribe(
            data => {
                if (data.get('bulan')) {
                    this.bulan = data.get('bulan');
                }
                if (data.get('tahun')) {
                    this.tahun = data.get('tahun');
                }
            }
        )


        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
        })
        this.getPreeData()
    }

    getPreeData() {
        this.spinner.show()
        this.absensiJadwalTipeService.getAbsensiJadwalTipes().subscribe(
            data => {
                this.listTipe = data.data
                this.spinner.hide()
            }
        )
    }

    pilihTipe() {
        this.spinner.show()
        if (!this.id_jadwal_tipe) {
            this.pesan.pesanWarningForm("Jadwal tipe harus di pilih sebagai template dasar");
        }

        let param: any = {
            id_pegawai: this.id_pegawai,
            id_jadwal_tipe: this.id_jadwal_tipe,
            bulan: this.bulan,
            tahun: this.tahun
        }
        this.absensiJadwalPegawaiTerjadwalService.postAbsensiJadwalPegawaiTerjadwals(param).subscribe(
            data => {
                this.goToBack();
                this.pesan.pesanBerhasilForm("Berhasil di setting template, silahkan update pda menu View");
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanBerhasilForm("Gagal");
                this.spinner.hide()
            }
        )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'pengaturan-absensi', 'view'])
    }



}
