import { Component, OnInit } from '@angular/core';
import { DirektoriPegawaiService, AbsensiJadwalTipeService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';
import { PegawaiDetailService } from '../../service/pegawai-detail.service';
import { NzMessageService } from 'ng-zorro-antd';
import { Pegawai, Jadwal_tipe } from 'src/app/sdk/hr/models';
import { FormGroupTypeSafe, FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { FormControl, Validators } from '@angular/forms';
import { PesanService } from 'src/app/services/tool/pesan.service';

@Component({
    selector: 'app-edit-tipe',
    templateUrl: './edit-tipe.component.html',
    styleUrls: ['./edit-tipe.component.scss']
})
export class EditTipeComponent implements OnInit {
    constructor(
        private direktoriPegawaiService: DirektoriPegawaiService,
        private absensiJadwalTipeService: AbsensiJadwalTipeService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private pegawaiDetailService: PegawaiDetailService,
        private nzMessageService: NzMessageService
    ) { }

    id_pegawai: string = ''
    pegawai: Pegawai = {}
    listTipe: Jadwal_tipe[] = []

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.activatedRoute.paramMap.subscribe(data => {
            if (data.get('id_pegawai')) {
                this.id_pegawai = data.get('id_pegawai')
                console.log(data.get('id_pegawai'))

                this.getPreeData()
                this.getData()
                this.form()
            }
        })
    }

    getPreeData(){
        this.absensiJadwalTipeService.getAbsensiJadwalTipes().subscribe(
            data=> {
                this.listTipe= data.data
            }
        )
    }
   getData(){
       this.direktoriPegawaiService.getDirektoriPegawaisId(this.id_pegawai).subscribe(
           data=> {
               this.dataForm.setValue(data.data)
           }
       )
   }

   dataForm: FormGroupTypeSafe<Pegawai>
    form() {
        this.dataForm = this.fb.group<Pegawai>(
            {
                no_induk: new FormControl(''),
                no_absensi: new FormControl(''),
                nama_belakang: new FormControl(''),
                nama_depan: new FormControl(''),
                jenis_kelamin: new FormControl(''),
                kewarganegaraan: new FormControl(''),
                type_pegawai: new FormControl(''),
                tanggal_masuk: new FormControl(''),
                hp: new FormControl(''),
                id_ptkp: new FormControl(''),
                id_jadwal_tipe: new FormControl('', [Validators.required]),
                email: new FormControl(''),
                status_pegawai: new FormControl(''),
                id_pegawai: new FormControl(''),
                bpjs_jht: new FormControl(''),
                bpjs_jkk: new FormControl(''),
                bpjs_jkm: new FormControl(''),
                bpjs_jp: new FormControl(''),
                id_klaim_kategori: new FormControl(''),
                bpjs_kesehatan: new FormControl(''),
                id_user_create: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                tanggal_keluar: new FormControl(''),
                foto: new FormControl('')
            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.pegawai = this.dataForm.value
        this.direktoriPegawaiService
            .putDirektoriPegawaisId({id: this.id_pegawai, body: this.pegawai})
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'pengaturan-absensi', 'view'])
    }
}
