import { DetailManualComponent } from './detail-manual/detail-manual.component';
import { EditManualComponent } from './edit-manual/edit-manual.component';
import { ViewComponent } from './view/view.component';
import { EditTipeComponent } from './edit-tipe/edit-tipe.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DetailTipeComponent } from './detail-tipe/detail-tipe.component';


const routes: Routes = [
    {
        path: '',
        redirectTo: 'list'
    },
    {
        path: 'edit-tipe/:id_pegawai',
        component: EditTipeComponent,
    },
    {
        path: 'detail-tipe',
        component: DetailTipeComponent,
    },
    {
        path: 'view',
        component: ViewComponent,
    },
    {
        path: 'edit-manual/:bulan/:tahun',
        component: EditManualComponent,
    },
    {
        path: 'detail-manual',
        component: DetailManualComponent,
    },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class PengaturanAbsensiRoutingModule { }
