import { Component, OnInit } from '@angular/core';
import { DirektoriPegawaiService, AbsensiJadwalTipeService, AbsensiJadwalPegawaiTerjadwalService, AbsensiJadwalTipeNormalDetailService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';
import { PegawaiDetailService } from '../../service/pegawai-detail.service';
import { NzMessageService } from 'ng-zorro-antd';
import { Pegawai, Jadwal_pegawai_terjadwal, Jadwal_tipe } from 'src/app/sdk/hr/models';

@Component({
  selector: 'app-detail-tipe',
  templateUrl: './detail-tipe.component.html',
  styleUrls: ['./detail-tipe.component.scss']
})
export class DetailTipeComponent implements OnInit {

    constructor(
        private direktoriPegawaiService: DirektoriPegawaiService,
        private absensiJadwalTipeService: AbsensiJadwalTipeService,
        private absensiJadwalPegawaiTerjadwalService: AbsensiJadwalPegawaiTerjadwalService,
        private absensiJadwalTipeNormalDetailService: AbsensiJadwalTipeNormalDetailService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private pegawaiDetailService: PegawaiDetailService,
        private nzMessageService: NzMessageService
    ) { }

    id_pegawai: string = ''
    id_jadwal_tipe: string = ''

    pegawai: Pegawai = {}
    datajadwalPegawai: Jadwal_pegawai_terjadwal [] = []

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })

        this.getPegawai()
        this.getItem()
    }

    getPegawai() {
        this.spinner.show()
        this.direktoriPegawaiService.getDirektoriPegawaisId(this.id_pegawai)
            .subscribe(data => {
                this.pegawai = data.data
                this.getTipeJamKerja(data.data.id_jadwal_tipe)
                this.spinner.hide()
            })
    }

    jadwalTipe: Jadwal_tipe = {}
    getTipeJamKerja(id_jadwal_tipe) {
        this.absensiJadwalTipeService.getAbsensiJadwalTipesId(id_jadwal_tipe).subscribe(
            data=> {
                this.jadwalTipe= data.data
            }
        )
    }

    listJadwakType: any[] = []
    numberToHari = ['Minggu', 'Senin', 'Selasa', 'Rabu', 'Kamis', 'Jumat', 'Sabtu'];
    getItem() {
        let filter = {
            id_jadwal_tipe: this.id_jadwal_tipe,
        }
        this.absensiJadwalTipeNormalDetailService.getAbsensiJadwalTipeNormalDetails(JSON.stringify(filter)).subscribe(
            data => {

                let out = [];
                data.data.forEach(item => {
                    let param:any = item;
                    param['nama_hari'] = this.numberToHari[parseInt(item.hari) - 1];

                    if(param.status_jadwal=='ya')
                    {
                        param.status_jadwal= true;
                    }else(
                        param.status_jadwal = false
                    )
                    out.push(param);
                })

                this.listJadwakType = out;

            }
        )
    }



    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'pengaturan-cuti', 'view'])
    }

}
