import { DetailManualComponent } from './../detail-manual/detail-manual.component';
import { Component, OnInit } from '@angular/core';
import { DirektoriPegawaiService, AbsensiJadwalTipeService, AbsensiJadwalPegawaiTerjadwalService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';
import { PegawaiDetailService } from '../../service/pegawai-detail.service';
import { NzMessageService, NzDrawerService } from 'ng-zorro-antd';
import { Pegawai, Jadwal_tipe, Jadwal_pegawai_terjadwal } from 'src/app/sdk/hr/models';

@Component({
    selector: 'app-view',
    templateUrl: './view.component.html',
    styleUrls: ['./view.component.scss']
})
export class ViewComponent implements OnInit {

    constructor(
        private direktoriPegawaiService: DirektoriPegawaiService,
        private absensiJadwalTipeService: AbsensiJadwalTipeService,
        private absensiJadwalPegawaiTerjadwalService: AbsensiJadwalPegawaiTerjadwalService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private pegawaiDetailService: PegawaiDetailService,
        private nzMessageService: NzMessageService,
        private drawerService: NzDrawerService
    ) { }

    id_pegawai: string = ''

    pegawai: Pegawai = {}
    datajadwalPegawai: Jadwal_pegawai_terjadwal[] = []

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })

        this.getPegawai()
        this.getJadwalPegawai()
    }

    getPegawai() {
        this.spinner.show()
        this.direktoriPegawaiService.getDirektoriPegawaisId(this.id_pegawai)
            .subscribe(data => {
                this.pegawai = data.data
                this.getTipeJamKerja(data.data.id_jadwal_tipe)
                this.spinner.hide()
            })
    }

    jadwalTipe: Jadwal_tipe = {}
    getTipeJamKerja(id_jadwal_tipe) {
        this.absensiJadwalTipeService.getAbsensiJadwalTipesId(id_jadwal_tipe).subscribe(
            data => {
                this.jadwalTipe = data.data
            }
        )
    }

    getJadwalPegawai() {
        this.spinner.show()
        this.absensiJadwalPegawaiTerjadwalService.getAbsensiJadwalPegawaiTerjadwals(JSON.stringify({ id_pegawai: this.id_pegawai })).subscribe(
            data => {
                this.datajadwalPegawai = data.data
                this.spinner.hide()
            }
        )
    }


    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'pengaturan-absensi', 'view'])
    }


    // ##update detail
    value:string="";
    openComponent(bulan:string,tahun:string): void {
        const drawerRef = this.drawerService.create<DetailManualComponent, { bulan: string,id_pegawai: string,tahun: string }, string>({
            nzTitle: 'Schedule Update',
            nzWidth:'1000px',
            nzContent: DetailManualComponent,
            nzContentParams: {
                bulan: bulan,
                id_pegawai: this.id_pegawai,
                tahun: tahun,
            }
        });

        drawerRef.afterOpen.subscribe(() => {
            console.log('Drawer(Component) open');
        });

        drawerRef.afterClose.subscribe(data => {
            console.log(data);
            if (typeof data === 'string') {
                this.value = data;
            }
        });
    }
}

